<template lang="html">

  <ul class="redes-cat flex row justify-start">
    <li><a :href="'https://www.linkedin.com/shareArticle?mini=false&url='+url" class="fa fa-linkedin" target="_blank"></a></li>
    <li><a :href="'http://www.facebook.com/sharer.php?u='+url+'&t='+textos[data]" target="_blank"><i class="fa fa-facebook"></i></a></li>
    <li><a target="_blank" :href="'http://twitter.com/intent/tweet?text='+textos[data]+'&url='+url"><i class="fa fa-twitter"></i></a></li>
    <li><a :href="'http://pinterest.com/pin/create/link/?url='+url" class="fa fa-pinterest-p" target="_blank"></a></li>
    
  </ul> 

</template>

<script lang="js">

  import {mapGetters} from 'vuex'

  export default  {
    name: 'rrss-share',
    props: ['texto','data'],
    mounted() {

    },
    data() {
      return {
        textos : {
            'junta-turbo' : 'Juegos de juntas para turbo Ajusa',
            'ajusil' : 'Sellante de silicona Ajusa',
            'arbol-levas' : 'rboles de levas Ajusa',
            'junta-culata' : 'Juntas de culata Ajusa',
            'junta-motor' : 'Juegos de juntas para el motor Ajusa',
            'otros' : 'Vlvulas EGR Ajusa',
            'retenes' : 'Retenes Ajusa',
            'taques' : 'Taqus hidrulicos Ajusa',
            'tornillos' : 'Tornillos de culata Ajusa',
            'tubos-engrase' : 'Tubos de engrase para turbos Ajusa',
            'turbo-fitting' : 'Turbo fitting Ajusa',
            'ajusil-en' : 'Silicone sealant Ajusa',
            'camshafts' : 'Camshafts Ajusa',
            'cylinder-head' : 'Cylinder Head Gaskets Ajusa',
            'gaskets-full' : 'Full Gaskets set Ajusa',
            'others' : 'Other components Ajusa',
            'oils-seals' : 'Oil seals Ajusa',
            'hydraulic-lifters' : 'Hydraulic lifters Ajusa',
            'cylinder-head-bolts' : 'Cylinder Head Bolts Ajusa',
            'oil-feed' : 'Oil feed Ajusa'
        }
      }
    },
    methods: {

    },
    computed : {
      ...mapGetters(['getLanguage']),
      url : function (){
        let url = location.origin+'/static/shared/'+this.data+".html";
        return url;
      }
    }
  }
</script>

<style scoped>
.rrss-share {

}
</style>



