<template lang="html">

 <div class="language">
  <a @click="requestLangChange">
    <img class="bandera" :src="`/static/img/change-to-${this.getLanguage}.png`" alt="cambiar idioma">
  </a>
</div>

</template>

<script lang="js">

  import {mapGetters,mapActions} from 'vuex';

  import texts from '@/translations'

  import router from '@/router'

  import Vue from 'vue'
  import axios from 'axios'
  import VueAxios from 'vue-axios'

  Vue.use(VueAxios, axios);

  export default  {
    name: 'idioma',
    props: [],
    data(){
      return {
        oldLang : ''
      }
    },
    methods: {
      requestLangChange : function(){

        this.oldLang = this.getLanguage;

        this.changeLanguage(this.getLanguage)
        .then(r => {
          this.$i18n.locale = r;
          this.changePath(r); 
        }).catch(e =>{
          console.log(e);
        })
      },
      changePath : function (newLanguage){

        let enlaces = texts[this.oldLang].enlaces;
        let path;
        let newPath;

        let forbidden = ["/noticia/", "/news/"];

        for(let e in enlaces){
          (enlaces[e] == this.$route.path) ? path = e : '';
        }

        if(!!path){
          newPath = texts[this.getLanguage].enlaces[path];
          router.push(newPath); 
        }else{

          if(forbidden.some(e => this.$route.path.includes(e))){

            axios.get(`post/${this.$route.params.id}`)
            .then(response => {
              let slugs = response.data.noticia.slugs;
              newPath = "/blog/";
              if(newLanguage == "es"){
                (!!slugs.es) ? newPath += `noticia/${slugs.es}` : '';
              }else{
                (!!slugs.en) ? newPath += `news/${slugs.en}` : '';
              }
              router.push(newPath); 
            })
            .catch(error => console.log(error)); 

          }

        }


      },
      ...mapActions(['changeLanguage']),
    },
    computed: {
     ...mapGetters(['getServer', 'getLanguage', 'getTexts'])
   }
 }
</script>

<style scoped>
.language {
  text-align: right;
} 
.bandera{
  max-width: 40px;
  width: 100%;
}

@media screen and (max-width: 1024px){
  .language{
    text-align: center;
  }
}
@media only screen and (max-width: 500px) {
  .bandera{
    width: 30px;
  }
}

</style>



